.wrapper {
    padding: 20px;
    width: 320px;
}

.header {
    display: flex;
    justify-content: space-between;
    align-items: center;

}

.avatar {
    width: 44px;
    height: 44px;
    border-radius: 50%;
    object-fit: cover; 
}

.follow-btn {
    padding: 6px 16px;    
}

.body {
    margin-top: 12px;
    color: #161823;
}

.nickname {
    font-size: 1.8rem;
}

.check {
    font-size: 1.4rem;
    color: #20d5ec;
    margin-left: 4px;
}

.name {
    font-size: 1.4rem;
}

.analytics {
    margin-top: 8px;
    font-size: 1.7rem;
}

.value ~ .value {
    margin-left: 1.5rem;
}

.label {
    color: #161823bf;
}